$main-grey: #CEE2E7;
$main-grey-alt: #c3ddde;
$main-grey-equ: #bdd5dc;
$darkgrey: #688A93;
$main-bggrey: #ECF3F5;
$grey-txt: #A4A4A4;
$main-yellow: #E6E183;
$main-green: #9DD6BE;
$main-red: #EEB4A7;
$main-blue: #8FBCFF;
$main-transparent: rgba(255, 255, 255, 0.95);
$title: #E7504F;

