* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

a {
    text-decoration: none;
    font-size: 16px;
    // text-transform: uppercase;
    line-height: 24px;
    color: $darkgrey;

    &:hover {
        color: black;
    }
}

body {
    font-family: 'PT Sans', sans-serif;
    // font-family: 'PTRootRegular', 'PT Sans', sans-serif;
}

.wrap {
    display: flex;
    width: 100%;
    padding: 50px 25px;
}

.container {
    width: 100%;
    padding: 0 25px;
    position: relative;
}

.lettersblock {
    position: absolute;
    padding: 0 0 50px 0;
}

.hidden {
    display: none;
}

.flexcontainer {
    display: flex;
    flex-wrap: wrap;
    gap: 1px;
}

.togglebtnsruen {
    align-self: flex-end;
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    background-color: white;
    border: none;
    padding: 6px 10px;
    margin: 9px 5px 20px 0;
    cursor: pointer;

    &:hover {
        background-color: $darkgrey;
        color: white;
    }
}

.togglebtnruensactive {
    background-color: $darkgrey;
    color: white;
    cursor: default;
}

.togglebtns {
    align-self: flex-end;
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    background-color: $main-bggrey;
    border: none;
    padding: 6px 10px;
    margin: 9px 5px;
    cursor: pointer;

    &:hover {
        background-color: $darkgrey;
        color: white;
    }
}

.togglebtnsactive {
    background-color: $darkgrey;
    color: white;
    cursor: default;
}

.infocontainer {
    display: flex;
    width: 100%;
}

.langmenu {
    padding: 20px 30px;
    white-space: nowrap;
    background: $main-bggrey;
    list-style-type: none;
    // min-width: 235px;
    // border-radius: 10px;
    // column-count: 4;
    // column-gap: 50px;
    // column-rule: 1px solid white;
    // box-shadow: 3px 3px 12px rgba(0, 0, 0, 0.288);
}

.langBtn {
    background: $main-grey;
    border: none;
    padding: 8px 12px;
    margin-top: 15px;
    color: white;
    font-size: 32px;
    cursor: pointer;
}

.langtitle {
    font-size: 64px;
    line-height: 83px;
}

.innercontainer {
    display: flex;
    width: 100%;
}

.langlist {
    font-size: 18px;
    line-height: 36px;
}

.langllistparams {
    list-style-type: none;
}

.langlisttitle {
    color: $title;
    // border-bottom: 1px solid $main-grey;
    padding-top: 30px;
}


.altborder {
    outline: 2px solid $darkgrey;
    outline-offset: -1px;
    display: flex;
    flex-wrap: nowrap;
}

.altborder_in {
    outline: 4px solid white;
    outline-offset: -4px;
    display: flex;
    flex-wrap: nowrap;
    gap: 1px;
}

.smalltitle {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    padding: 35px 15px 15px 0;
}

.smallunititle {
    padding: 8px;
}

.letter {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: center;
    width: 90px;
    // width: 8.14%;
    height: 90px;
    background: $main-grey;
    // margin: 0 1px 1px 0;
}

.letter:hover {
    opacity: 0.7;
    position: relative;
    filter: alpha(opacity=70);
}

.russiansign {
    background: $main-grey;
}

.notrussiansign {
    // background: $main-yellow;
    background: $main-grey;
}

.dialectsign {
    background: $main-green;
}

.oldersign {
    background: $main-red;
}

.lexicosign {
    background: $main-blue;
}

.altsign {
    background: $main-grey-alt;
}

.equsign {
    background: $main-grey-equ;
}

.featuresign {
    font-feature-settings: "aalt";
}

.unicode {
    padding-bottom: 10px;
    color: rgb(149, 149, 149);
    font-size: 12px;
}

.bigletter {
    // font-family: 'Octava', 'PT Sans', sans-serif;
    // font-family: 'PTRootRegular', sans-serif;
    font-size: 32px;
    padding-top: 23px;
}

.biguniletter {
    // font-family: 'Octava', 'PT Sans', sans-serif;
    // font-family: 'PTRootRegular', sans-serif;
    font-size: 32px;
    padding-bottom: 23px;
}

.markplace {
    text-align: right;
    // padding: 0 5px 10px 10px;
    margin: -4px 6px 10px 5px;
}

.altmark {
    font-size: 10px;
    padding: 1px 6px 3px 6px;
    border-radius: 10px;
    color: $grey-txt;
    background: $main-transparent;
}

.langinfo {
    font-size: 24px;
    line-height: 31px;
    padding: 40px 0 10px 0;
    width: 618px;
}

.legendbg {
    padding: 17px;
    border-radius: 10px;
    background: $main-bggrey;
    margin: 40px 0 -57px 20px;
    list-style-type: none;

}

.colorsample {
    width: 15px;
    height: 15px;
}

.legenditem {
    display: flex;
    padding: 3px;
}

.legendtext {
    font-size: 12px;
    color: #6F6E6E;
    text-transform: uppercase;
    margin-left: 15px;
}

@media (max-width:1200px) {

    .langinfo {
        width: 440px;
    }
}

@media (max-width:1036px) {

    .langinfo {
        width: 100%;
    }

    .innercontainer {
        display: block;
    }

    .legendbg {
        margin: 40px 0 0 0;
        column-count: 2;
        column-gap: 20px;
    }

    // .langmenu {
    //     column-count: 3;
    // }

}


