* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a {
  text-decoration: none;
  font-size: 16px;
  line-height: 24px;
  color: #688A93;
}

a:hover {
  color: black;
}

body {
  font-family: 'PT Sans', sans-serif;
}

.wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  padding: 50px 25px;
}

.container {
  width: 100%;
  padding: 0 25px;
  position: relative;
}

.lettersblock {
  position: absolute;
  padding: 0 0 50px 0;
}

.hidden {
  display: none;
}

.flexcontainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 1px;
}

.togglebtnsruen {
  -ms-flex-item-align: end;
      align-self: flex-end;
  font-size: 12px;
  color: #6F6E6E;
  text-transform: uppercase;
  background-color: white;
  border: none;
  padding: 6px 10px;
  margin: 9px 5px 20px 0;
  cursor: pointer;
}

.togglebtnsruen:hover {
  background-color: #688A93;
  color: white;
}

.togglebtnruensactive {
  background-color: #688A93;
  color: white;
  cursor: default;
}

.togglebtns {
  -ms-flex-item-align: end;
      align-self: flex-end;
  font-size: 12px;
  color: #6F6E6E;
  text-transform: uppercase;
  background-color: #ECF3F5;
  border: none;
  padding: 6px 10px;
  margin: 9px 5px;
  cursor: pointer;
}

.togglebtns:hover {
  background-color: #688A93;
  color: white;
}

.togglebtnsactive {
  background-color: #688A93;
  color: white;
  cursor: default;
}

.infocontainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.langmenu {
  padding: 20px 30px;
  white-space: nowrap;
  background: #ECF3F5;
  list-style-type: none;
}

.langBtn {
  background: #CEE2E7;
  border: none;
  padding: 8px 12px;
  margin-top: 15px;
  color: white;
  font-size: 32px;
  cursor: pointer;
}

.langtitle {
  font-size: 64px;
  line-height: 83px;
}

.innercontainer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.langlist {
  font-size: 18px;
  line-height: 36px;
}

.langllistparams {
  list-style-type: none;
}

.langlisttitle {
  color: #E7504F;
  padding-top: 30px;
}

.altborder {
  outline: 2px solid #688A93;
  outline-offset: -1px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.altborder_in {
  outline: 4px solid white;
  outline-offset: -4px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  gap: 1px;
}

.smalltitle {
  font-size: 12px;
  color: #6F6E6E;
  text-transform: uppercase;
  padding: 35px 15px 15px 0;
}

.smallunititle {
  padding: 8px;
}

.letter {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  text-align: center;
  width: 90px;
  height: 90px;
  background: #CEE2E7;
}

.letter:hover {
  opacity: 0.7;
  position: relative;
  filter: alpha(opacity=70);
}

.russiansign {
  background: #CEE2E7;
}

.notrussiansign {
  background: #CEE2E7;
}

.dialectsign {
  background: #9DD6BE;
}

.oldersign {
  background: #EEB4A7;
}

.lexicosign {
  background: #8FBCFF;
}

.altsign {
  background: #c3ddde;
}

.equsign {
  background: #bdd5dc;
}

.featuresign {
  -webkit-font-feature-settings: "aalt";
          font-feature-settings: "aalt";
}

.unicode {
  padding-bottom: 10px;
  color: #959595;
  font-size: 12px;
}

.bigletter {
  font-size: 32px;
  padding-top: 23px;
}

.biguniletter {
  font-size: 32px;
  padding-bottom: 23px;
}

.markplace {
  text-align: right;
  margin: -4px 6px 10px 5px;
}

.altmark {
  font-size: 10px;
  padding: 1px 6px 3px 6px;
  border-radius: 10px;
  color: #A4A4A4;
  background: rgba(255, 255, 255, 0.95);
}

.langinfo {
  font-size: 24px;
  line-height: 31px;
  padding: 40px 0 10px 0;
  width: 618px;
}

.legendbg {
  padding: 17px;
  border-radius: 10px;
  background: #ECF3F5;
  margin: 40px 0 -57px 20px;
  list-style-type: none;
}

.colorsample {
  width: 15px;
  height: 15px;
}

.legenditem {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 3px;
}

.legendtext {
  font-size: 12px;
  color: #6F6E6E;
  text-transform: uppercase;
  margin-left: 15px;
}

@media (max-width: 1200px) {
  .langinfo {
    width: 440px;
  }
}

@media (max-width: 1036px) {
  .langinfo {
    width: 100%;
  }
  .innercontainer {
    display: block;
  }
  .legendbg {
    margin: 40px 0 0 0;
    -webkit-column-count: 2;
            column-count: 2;
    -webkit-column-gap: 20px;
            column-gap: 20px;
  }
}
